@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&display=swap);
body {
  margin: 0;
  font-family: "Roboto Slab", serif;
  overflow-y: auto; /* Hide vertical scrollbar */
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.home {
  background: rgba(12, 13, 23);
  color: white;
  width: 100vw;
  top: 0;
  position: -webkit-sticky;
  position: sticky;
  z-index: 1;
  box-shadow: 0px 10px 53px rgba(0, 0, 0, 0.3);
}

.nav li {
  list-style: none;
  padding: 20px 0;
  cursor: pointer;
  transition: all 700ms ease-in-out;
}

#navbar-wrapper {
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.nav {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin: 0 40px;
}
.link {
  text-decoration: none;
  color: white;
  margin: 60px;
  text-transform: uppercase;
  font-size: 30px;
}
.link-black {
  color: black;
  text-decoration: none;
  margin: 60px;
  text-transform: uppercase;
  font-size: 30px;
}
li:hover {
  transform: scale(1.2);
}
.link:hover {
  color: orange;
}

.moon {
  color: blueviolet;
  font-size: 20px;
}
.sun {
  color: orange;
  font-size: 20px;
}
.checkbox {
  opacity: 0;
  position: absolute;
}

.checkbox:checked + .label__checkbox .ball {
  transform: translateX(24px);
}
.label__checkbox {
  background: #111;
  cursor: pointer;
  height: 26px;
  width: 50px;
  display: flex;
  border-radius: 50px;
  align-items: center;
  justify-content: space-between;
  padding: 5px;
  position: relative;
}
.ball {
  width: 20px;
  position: absolute;
  top: 2px;
  left: 2px;
  height: 20px;
  background-color: white;
  border-radius: 50%;
}

@media only screen and (max-width: 600px) {
  .home {
    width: 410px;
  }
  .link {
    margin: 5px;
    font-size: 12px;
  }

  .nav {
    margin: 0 10px;
    justify-content: center;
  }
  #navbar-wrapper {
    justify-content: space-between;
  }
}
@media (min-width: 415px) and (max-width: 800px) {
  .home {
    width: 1400px;
    max-width: 1800px;
  }

  .link {
    font-size: 30px;
    margin: 0 30px;
  }
}

@media (min-width: 1025px) and (max-width: 1281px) {
  .home {
    width: 1300px;
  }
}

@media (min-width: 768px) and (max-width: 1024px) {
  .home {
    width: 1300px;
  }
}

.about {
  background: white;
  color: black;
  width: 100vw;
  font-family: "Dancing Script", cursive;
  z-index: -1;
}

.about-headline {
  font-size: 50px;
  border-bottom: 2px solid #ccc;
  padding-bottom: 6px;
  position: relative;
}

.about-headline::after {
  content: "";
  position: absolute;
  width: 120px;
  height: 2px;
  background: blue;
  left: 0;
  bottom: -1px;
  transition: all 1s ease;
}

.about-headline:hover::after {
  width: 100%;
}
.about-container {
  max-width: 1300px;
  margin: 40px auto;
}

.button-element {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  align-items: center;
}

.about-my-location {
  display: grid;
  grid-template-columns: repeat(2, 0.5fr);
  align-items: center;
}

.my-location-icon {
  font-size: 30px;
  color: red;
}

.location-headline {
  font-size: 30px;
}
.about-btn {
  font-family: "Dancing Script", cursive;
  font-size: 20px;
  padding: 30px 20px;
  position: relative;
  border-radius: 5px;
  padding: 20px;
  cursor: pointer;
  outline: none;
}

.about-btn.btn-slide::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: -1;
  background-color: orange;
  transition: transform 1s ease-in-out;
  transform: scaleX(0);
  transform-origin: left;
  overflow: hidden;
}

.about-btn.btn-slide:hover::before,
.about-btn.btn-slide:focus::before {
  transform: scaleX(1);
}

.about-btn.btn-slide {
  transition: color 300ms ease-in-out;
  z-index: 1;
}

.about-para {
  color: #000;
  margin: 20px auto;
  padding: 0 30px 15px 0px;
  font-size: 30px;
}
.about-inside {
  display: flex;
  justify-content: center;
  align-items: center;
}

.skillImages {
  width: 200px;
  height: 200px;
  border-radius: 60%;
  box-shadow: 0 10px 53px rgba(0, 0, 0, 0.3);
  object-fit: contain;
}

.about-my-skills {
  text-align: center;
  padding: 20px;
  font-size: 40px;
  border-bottom: 2px solid #ccc;
  width: 40%;
  margin: 0 auto;
}
.about-skills {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 30px;
  margin: 100px 0;
}

@media only screen and (max-width: 600px) {
  .about-skills {
    grid-template-columns: repeat(2, 1fr);
  }
  .skillImages {
    height: 130px;
    width: 130px;
  }
  .about-inside {
    flex-direction: column;
    width: 320px;
  }
  .about-para {
    font-size: 20px;
    margin: 20px auto;
    padding: 0 20px 11px 0px;
  }
  .about-headline {
    font-size: 30px;
  }
  .my-location-icon {
    font-size: 20px;
  }

  .about-btn {
    padding: 10px;
    font-size: 18px;
  }

  .location-headline {
    font-size: 20px;
  }
  .about-image {
    width: 280px;
    height: 280px;
  }
  .about-container {
    width: 300px;
  }
}

@media (min-width: 415px) and (max-width: 800px) {
  .about {
    width: 1400px;
  }
  .button-element {
    grid-gap: 60px;
  }
  .about-btn {
    padding: 0 60px;
  }
  .about-inside {
    width: 800px;
    flex-direction: row;
    justify-content: space-between;
  }
  .about-container {
    width: 900px;
  }
  .about-para {
    font-size: 30px;
  }

  .about-skills {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (min-width: 1025) and (max-width: 1250px) {
  .about {
    width: 1500px;
  }
}
@media (min-width: 768px) and (max-width: 1024px) {
  .about {
    width: 1300px;
  }
}

@media (min-width: 1025px) and (max-width: 1281px) {
  .about {
    width: 1300px;
  }
}

#project {
  font-family: "Open Sans", sans-serif;
  background: white;
  margin: 30px;
  padding: 30px 10px;
  height: 440px;
  width: 500px;
  position: relative;
  box-shadow: 0px 10px 53px rgba(0, 0, 0, 0.3);
  border-radius: 15px;
}

.project-headline {
  padding-bottom: 10px;
}

.project-headline-border-bottom {
  width: 70px;
  height: 2px;
  margin: 0px auto;
  border-bottom: 1px solid orange;
}

.project-content {
  display: flex;
  flex-direction: column;
  margin: 10px;
}

.project-discription {
  padding: 10px 0;
}

.project-using {
  margin: 10px 0;
}
.project-containter {
  display: flex;
  justify-content: center;
  flex-direction: column;
  padding: 10px;
}

.live-demo-btn {
  padding: 10px;
  margin: 5px 0;
  font-family: "Open Sans", sans-serif;
  font-size: 20px;
  color: black;
  outline: none;
  transition: all 500ms ease-in-out;
  border-radius: 6px;
  border: none;
  background: rgba(0, 22, 22, 0.3);
  cursor: pointer;
  text-decoration: none;
}

.live-demo-btn:hover {
  transform: scale(1.2);
  background: orange;
}

.live-demo-container {
  position: absolute;
  margin-left: 25%;
  margin-right: 25%;
}

@media only screen and (max-width: 600px) {
  #project {
    width: 350px;
    height: 420px;
  }
}

@media (min-width: 768px) and (max-width: 1024px) {
  #project {
    width: 1300px;
  }
}

.work {
  background-color: #e9ddc7;
  font-family: "Open Sans", sans-serif;
  height: 700px;
  width: 100%;
}

.work-container {
  width: 1300px;
  margin: 0 auto;
}

.Work-headline {
  font-size: 50px;
  border-bottom: 1px solid #333;
  padding-bottom: 5px;
  position: relative;
  width: 400px;
  text-align: center;
  margin: 20px auto;
}

.single-projects {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  margin: 40px 20px;
}

.Work-headline::after {
  content: "";
  position: absolute;
  width: 200px;
  height: 2px;
  background: orange;
  left: 0;
  bottom: -1px;
  transition: all 1s ease;
}

.Work-headline:hover::after {
  width: 100%;
}

@media only screen and (max-width: 600px) {
  .work {
    height: 1550px;
    width: 400px;
  }
  .work-container {
    width: 400px;
  }

  .Work-headline::after {
    width: 130px;
  }
  .Work-headline {
    font-size: 40px;
    text-align: center;
    width: 270px;
  }
  .single-projects {
    flex-direction: column;
    margin: 0px auto;
  }
}

@media (min-width: 415px) and (max-width: 800px) {
  .work {
    width: 1400px;
    height: 700px;
  }

  .single-projects {
    flex-direction: row;
  }

  .work-container {
    width: 1400px;
  }
}

@media (min-width: 768px) and (max-width: 1024px) {
  .work {
    width: 1300px;
  }
}

@media (min-width: 1025px) and (max-width: 1281px) {
  .work {
    width: 1300px;
  }
}

